import { createWebHistory, createRouter } from 'vue-router';
import SPATestPage from '@/components/SPATestPage.vue'
import MainLogin   from '@/components/MainLogin.vue'

const routes = [
  { path: '/index', name: 'SPATestPage', component: SPATestPage, beforeEnter: function (to, from, next) { next() } },
  { path: '/login', name: 'MainLogin', component: MainLogin,   beforeEnter: function (to, from, next) { next() } },
  { path: '/loginff', name: 'SPATestPage', component: SPATestPage,   beforeEnter: function (to, from, next) { next() } },  
]

export const router = createRouter({
  history: createWebHistory(),
  routes,
});